import {
	mapState,
	mapMutations,
	mapGetters,
	mapActions
} from 'vuex';
import NumberAdd from '../../component/NumberAdd.vue'
export default {
	components: {
		NumberAdd
	},
	data() {
		return {
			redactGo: true, // 编辑按钮
			// shopAll: false, // 全选状态
			// 商品初始化转台
			step: 2, // 每次点击的值
			minGood: 1, // 最小值
			value: 1 // 输入框当前值

		}
	},
	computed: {
		// 检查是否是登入状态
		...mapState({
			loginStartus: state => state.user.loginStartus,
			goodList: state => state.cat.list,
			shopAll: state => state.cat.shopAll,
			sameitems:state=>state.cat.sameitems,
			catStorg:state=>state.cartStorg
		}),
			...mapGetters(['totalCount'])
	},
	watch:{
		goodList:{
			 handler(newName, oldName){
				 console.log('升读',newName)
			 },
			immediate: true,
			deep: true
		}
	},
	onLoad() {
		// 只初始化一次
	},
	onShow() {
		// 每次页面都会调用，
		if (this.loginStartus) {
			this.getCatData()
			console.log('list',this.goodList)
		}
		
	},
	// 下拉刷新在重新请求 购物车
	
	methods: {
		bindGetUserInfo(res){
			console.log(res.detail.userInfo)
			
		},
		...mapMutations(['initGetDate','setGoods', 'login','cheackAll','paymentItem',
		'selectedItem','selectedGood','remoeItem']),
		...mapActions(['actsetGoods']),
		// 获取购物车的数据
		getCatData() {
			// 获取本地存储
			let carData = uni.getStorageSync(this.catStorg)
			if(carData){
				this.initGetDate(JSON.parse(carData))
			}
			// this.$http.request({
			// 	url: '/cartlist',
			// 	// methods: 'POST',
			// 	headher: {
			// 		token: true
			// 	},
			// 	data:{
			// 		//商品店铺id，商品id，修改
			// 	},
			// }).then((res) => {
			// 	this.initGetDate(res.list)
			// 	console.log(res.list)
			// })

		},
		defGoods(){
			//  // 判断是否有选中的商品
			let state = this.pitchItem()
			console.log('删除',state)
			if (state){
				// 有选中商品
				// 提示是否确定要删除
				uni.showModal({
				    content: '确定要删除吗',
				    success: (e)=>{
				    	if(e.confirm){
				    		this.remoeItem()
				    	}
				    }
				});
			}else {
				this.$api.msg('您还未选中商品')
			}
			
		},
		navToLogin() {
			console.log('dengru ')
		},
		edit() {
			this.redactGo = !this.redactGo
		},
		numberChange(e,item) {
			item.shopNumber = Number(e)
			this.setGoods(item)
			 // 重新请求,保持与数据库一致，这是是修改数据库中的购物的商品数量
			this.getCatData()
		},
		check() {

		},
		buyShop(){
			// 结算， 获取选中的商品， 结算
			//  // 判断是否有选中的商品
			const state = this.pitchItem()
			console.log(this.pitchItem())
			if (state){
				// 有选中商品， 下单成功删除，这里不是删除，
				// 直接跳转付款页面
				this.paymentItem()
				uni.navigateTo({
					url:'/pages/orderItems/orderItems'
				})
				
			}else {
				this.$api.msg('您还未选中商品')
			}
		},
		//  // 判断是否有选中的商品
		pitchItem(){			
			let i= this.goodList.length;
			while(i--){
				let goods = this.goodList[i].gooddata
				if (goods.stated){
					return true
				}
				let j = goods.length;
				while(j--){
					if (goods[j].stated){
						return true
					}
				}
			}
			return false
		}
	},
	
}
